import {UserModel} from '../../model/user'
const userModel = new UserModel();
import WucTab from '@/components/wuc-tab/wuc-tab2.vue';
import { parse } from 'path';
	export default {
		data() {
			return {
				TabCur3: 0,
				tabList3: [{
					name: '全部'
				}, {
					name: '已上传'
				}, {
					name: '已到账'
				}],
				list1:[],
				list2:[],
				list3:[],
				stutas1:true,
				stutas2:true,
				stutas3:true,
				page1:1,
				page2:1,
				page3:1,
				size: 10,
				status : 0,
				imagePopup:false,
				imageUrl:"",
				flag: true,
				detailPopup:false,
				remarks : ""
			}
		},
		components: {
			WucTab
		},
		methods: {
			lower() {
				if(this.flag == true) {
					this.getList();
				}
			},
			up_card(record_id){
				uni.navigateTo({
					url:'/pagesB/park_upload/park_upload?record_id=' + record_id
				})
			},
			swiperChange3(e) {
				let {current} = e.target;
				this.TabCur3 = current;
				this.status = current;
				if(e.detail.current == 0 && this.page1 == 1) {
					this.getList();
				}
				if(e.detail.current == 1 && this.page2 == 1) {
					this.getList();
				}
				if(e.detail.current == 2 && this.page3 == 1) {
					this.getList();
				}
			},
			getList()
			{
				let params = {
					size: this.size,
					status: this.status
				}
				switch(this.TabCur3) {
					case 0 : 
						params.page = this.page1;
						break;
					case 1:
						params.page = this.page2;
						break;
					case 2:
						params.page = this.page3;
						break;
				}
				userModel.parkingRecode(params).then((res) => {
					if(res.code == 200) {
						if(this.TabCur3 == 0) {
							this.list1 = this.list1.concat(res.data)
                        	if(res.data.length==0){
								this.flag = false;
                        	}else{
								this.flag = true;
                            	this.page1 = this.page1+1;
							}
							console.log(this.list1.length);
						}
						if(this.TabCur3 == 1) {
							this.list2 = this.list2.concat(res.data)
							if(res.data.length==0) {
								this.flag = false;
							}else {
								this.flag = true;
								this.page2 = this.page2+1;
							}
						}
						if(this.TabCur3 == 2) {
							this.list3 = this.list3.concat(res.data)
							if(res.data.length == 0) {
								this.flag = false;
							}else {
								this.flag = true;
								this.page3 = this.page3+1
							}
						}
					}
				})
			},
			close(){
				this.imagePopup = false
			},
			checkImage(item){
				this.imagePopup = true
				this.imageUrl = item.apply.voucher_image
			},
			openDetails(item){
				this.detailPopup = true;
				this.remarks = item.apply.remarks
			},
			closeDetailsPopup(){
				this.detailPopup = false;
			}

		},
		onShow(){
			this.list1 = [];
			this.list2 = [];
			this.list3 = [];
			this.page1 = 1;
			this.page2 = 1;
			this.page3 = 1;
			this.getList();
		}
	}
